import * as yup from 'yup';

import { PhoneRegex } from 'utils/regex';

const sendContactScheme = yup.object({
  name: yup
    .string()
    .required(' ')
    .test('name', ' ', (value) => {
      return Boolean(value && value.trim());
    }),
  phone: yup
    .string()
    .required(' ')
    .matches(PhoneRegex, ' '),
  agree: yup
    .bool()
    .required()
    .oneOf([true], ''),
});

export default sendContactScheme;
